.contenedor{
    width:1170px;
    height: 368px;
    margin: 10px 10px 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    box-shadow: 0 3px 13px 1px rgb(0,0,0/9%) ;
}

.imagen{
    height: 80%;
    width: auto;
    align-items: flex-start;
}
.contenedor1-texto{
    text-align: center;
    padding: 40px;
    font-family: sans-serif;
    font-weight: 400;
    line-height: 1.5rem;
}
.nombre {
    font-size: 1.3rem;
    font-weight: 800;
}
.cargo-contenedor1{
    font-size: 1.3rem;
    margin-top: 15px;
    padding-bottom: 15px;
}
.texto-contenedor1{
    font-size: 1.1rem;
    text-align: justify;
}